
@import '../../assets/styles/media';






.input {
    background-color: #e7eaf2;
    opacity: 0.7;
    padding: 10px;

    font-size: 20px;
    border-radius: 10px;
    border: none;
    margin-bottom: 20px;

    &::placeholder {
        color: black;
        font-family: 'Dancing Script';
        font-weight: 700;
        font-size: 20px;

    }

    @include media-tablet {
        font-size: 24px;
        width: 100%;
        height: 60px;

        &::placeholder {
            font-size: 26px;
        }

    }

    @include media-desktop {
        &::placeholder {
            font-size: 36px;
        }
    }

}



.button {
    background-color: #e7eaf2;
    display: block;
    font-family: 'Dancing Script';
   
    margin-right: auto;
    padding: 10px;
    width: 150px;
    font-weight: 700;
    border: none;
    border-radius: 10px;

    @include media-tablet {
        font-size: 26px;


    }

    @include media-desktop {
        font-size: 36px;
        width: 250px;
    }
}



.form {

    width: 210px;

    padding-top: 55px;
    margin: 0 auto;

    @include media-tablet {
        margin-left: auto;
        margin-right: 0;
        width: 400px;
        padding-top: 55px;


    }

    @include media-desktop {

        width: 600px;
        padding-top: 100px;


    }

}

.background {
    height: 100vh;
    background-size: cover;
    background-position-x: right;
    background-image: url('../../assets/how-to-format-a-book-900.jpg');
}